<template>
    <div class="change-pwd">
        <label class="change-pwd-title"></label>
        <div class="change-pwd-content">
            <Form ref="userForm" :model="userForm" :rules="ruleValidate" label-position="top" style="width: 300px;">
                <FormItem label="" prop="oldpwd">
                    <Input type="password" v-model="userForm.oldpwd"/>
                </FormItem>
                <FormItem label="" prop="pwd">
                    <Input type="password" v-model="userForm.pwd"/>
                </FormItem>
                <FormItem label="" prop="repwd">
                    <Input type="password" v-model="userForm.repwd"/>
                </FormItem>
                <FormItem>
                    <Button type="primary" @click="changePwd"></Button>
                </FormItem>
            </Form>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            userForm:{
                oldpwd: '',
                pwd: '',
                repwd: ''
            },
            ruleValidate:{
                oldpwd: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                pwd: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                repwd: [
                    { required: true, message: '', trigger: 'blur' },
                    { validator: (rule, value, callback) => {
                        if(value!=this.userForm.pwd){
                            callback('')
                        }else{
                            callback();
                        }
                    }, trigger: 'blur'}
                ],
            }
        }
    },
    methods: { 
        changePwd(){
            this.$refs.userForm.validate(isOk =>{
                if(isOk){
                    this.$ajaxFetch.post('/changepwd', this.userForm, res=>{
                        if(res.code==0){
                            this.$Message.success('');
                            setTimeout(()=>{
                                location.reload();
                            }, 1000);
                        }else{
                            this.$Message.error(res.msg);
                        }
                    },error=>{
                        this.$Message.error(error);
                    },'');
                }
            })
        }
    }
}
</script>


<style  lang="less" scoped>
    .change-pwd{
        width: 100%;
        background-color: #fff;
        padding: 20px;
        .change-pwd-title{
            font-size: 18px;
        }
        .change-pwd-content{
            min-height: 300px;
            margin-top: 20px;
            padding-top: 20px;
            border-top: 1px solid #ddd;
            
        }
    }
</style>
